function createRAMLFile(title, version, baseUri, sample, raml1) {
    if (sample === void 0) { sample = true; }
    if (raml1 === void 0) { raml1 = true; }
    var apiLines = [(raml1 ? "#%RAML 1.0" : "#%RAML 0.8"), "title: " + title];
    if (version)
        apiLines.push("version: " + version);
    if (baseUri)
        apiLines.push("baseUri: " + baseUri);
    var typesLines = [
        'types:',
        '  TestType:',
        '    type: object',
        '    properties:',
        '      id: number',
        '      optional?: string',
        '      expanded:',
        '        type: object',
        '        properties:',
        '          count: number'
    ], resourceLines = [
        '/helloWorld:',
        '  get:',
        '    responses:',
        '      200:',
        '        body:',
        '          application/json:',
        '            example: |',
        '              {',
        '                "message" : "Hello World"',
        '              }'
    ];
    var result = apiLines;
    if (sample) {
        if (raml1)
            result = result.concat(typesLines);
        result = result.concat(resourceLines);
    }
    return result.join('\n') + '\n';
}
