function splitOnLines(text) {
    var lines = text.match(/^.*((\r\n|\n|\r)|$)/gm);
    return lines;
}
exports.splitOnLines = splitOnLines;
function cleanEmptyLines(text) {
    var lines = splitOnLines(text);
    var rs = [];
    for (var i = 0; i < lines.length; i++) {
        if (lines[i].trim().length > 0) {
            rs.push(lines[i]);
        }
    }
    return rs.join("");
}
exports.cleanEmptyLines = cleanEmptyLines;
//FIXME remove it from here duplication with jsyaml2lowLevel.ts
function stripIndent(text, indent) {
    var lines = splitOnLines(text);
    var rs = [];
    for (var i = 0; i < lines.length; i++) {
        if (i == 0) {
            rs.push(lines[0]);
        }
        else {
            rs.push(lines[i].substring(indent.length));
        }
    }
    return rs.join("");
}
